<template>
	<template-auth 
    titulo="Faça o seu login"
    :progressLoader="progressLoader"
    :alert="alertInfo"
  >
    <form @submit.prevent="logar">
      <div class="form-group">
        <v-text-field
          id="email"
          v-model="email"
          label="E-mail"
          :rules="emailRules"
          v-on:keyup.enter="focusToNext('password')"
          autocomplete="off"
          required 
        />
      </div>

      <div class="form-group">
        <v-text-field
          ref="password"
          v-model="password"
          :type="showPassword ? 'text' : 'password'"
          label="Senha"
          :append-icon="showPassword ? 'visibility_off' : 'remove_red_eye'"
          :rules="passwordRules"
          @click:append="showPassword = !showPassword"
          v-on:keyup.enter="enterClick"
          required 
        />
      </div>

      <div class="form-group">
        <v-switch
          v-model="isBarber"
          label="barbeiro?"
          color="secondary"
        ></v-switch>
      </div>

      <div class="text-center mt-4">
        <btn-confirm
          text="Logar"
          :disabled="btnDisabled"
          @clicked="login"
        />
      </div>

			<div class="text-center mt-4">
        <btn-text text="Recuperar senha" @clicked="goTo('recovery-password')" dark />
        <span class="d-block font-weight-light" light>ou</span>
				<btn-secondary
					class="mt-4"
          text="Cadastrar-se"
          @clicked="goTo('register')"
        />
      </div>
    </form>
  </template-auth>
</template>

<script>
// Modules
import Axios from "@/plugins/axios"
// Components
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import TemplateAuth from '@/pages/auth/template/Template'
// Mixins and MOdules
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import UserMixin from '@/mixins/UserMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Login',
  components: { BtnConfirm, BtnSecondary, BtnText, TemplateAuth },
  mixins: [ BarberMixin, BarbershopMixin, FormMixin, NavigationMixin, SnackbarMixin, UserMixin, ValidationMixin ],
  data: () => ({
    alertInfo: null,
    showPassword: false,
    email: '',
    isBarber: false,
    password: '',
    progressLoader: false  
  }),
  computed: {
    btnDisabled () {
      if (!this.isEmailValid(this.email)) return true
      if (!this.isPasswordValid(this.password)) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#email').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnAtivo) this.login()
      else this.setError('Preencha todos os dados corretamente!')
    },
    login () {
      this.progressLoader = true
      let url = this.isBarber ? '/auth/barber/login' : '/auth/user/login'
      Axios.post(url, {
        email: this.email,
        password: this.password
      })
      .then(response => {
        this.progressLoader = false
        if (response.data.codigo === 200) {
          let token = response.data.corpo
          if (this.isBarber) this.setBarber(token)
          else this.setUser(token)
        } else this.setError(response.data.corpo)
      })
      .catch ((err) => {
        window.console.log(err)
        this.setError('Não foi possível realizar login na aplicação!')
      })
    },
    setBarber (token) {
      let routePath = localStorage.routePathAdmin
      this.storeBarber(token)
      let barber = this.getBarber()
      if (barber) {
        if (barber.enabled) {
          if (routePath && routePath !== this.$route.path) this.goToPath(routePath)
          else this.goTo('admin')
        }
        else this.alertInfo = "Seu cadastro ainda não foi confirmado!!<br>Verifique sua caixa de e-mail e confirme o seu cadastro."
      } else this.setError('Não foi possível recuperar o token de acesso à aplicação!')
    },
    setError (msg) {
      this.alertInfo = null
      this.progressLoader = false
      this.showSnackbarErro(msg)
    },
    setUser (token) {
      let routePath = localStorage.routePathSite
      this.storeUser(token)
      let user = this.getUser()
      if (user) {
        if (user.enabled) {
          if (routePath && routePath !== this.$route.path) this.goToPath(routePath)
          else this.goTo('home')
        }
        else this.alertInfo = "Seu cadastro ainda não foi confirmado!!<br>Verifique sua caixa de e-mail e confirme o seu cadastro."
      } else this.setError('Não foi possível recuperar o token de acesso à aplicação!')
    }
  }
}
</script>